.width-area {
  width: 100%;
}
.container-area {
  max-width: 1440px;
  margin: auto;
  padding: 0 40px;
}

a {
  &:hover {
    color: inherit;
  }
}
button {
  &:focus {
    outline: none;
  }
}
img {
  width: 100%;
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  color: var(--color-text-root);
  font-weight: 400;
  font-family: "Roboto Regular";

  &.swal2-height-auto {
    height: 100% !important;
  }
  ::-webkit-scrollbar {
    width: 5px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: gray;
    border-radius: 10px;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: gray;
  }

