.ellipsis {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.form-search {
  //Select
  .ant-select {
    &-item {
      padding: 15px 20px;
    }
    &-arrow {
      right: 0 !important;
      top: 42% !important;
      left: 12px !important;
      svg {
        font-size: 1.5rem;
        color: var(--color-gray-icon);
      }
    }
  }
  .ant-picker-suffix {
    right: 0 !important;
    top: 27% !important;
    left: 8px !important;
    position: absolute;
    svg {
      font-size: 1.5rem;
      color: var(--color-gray-icon);
    }
  }

  .ant-select:not(.ant-select-disabled):hover .ant-select-selector {
    border: 1px solid #d9d9d9 !important;
  }
  .ant-select-single .ant-select-selector {
    height: 60px !important;
    border-radius: 10px !important;
  }
  .ant-select-single .ant-select-selector .ant-select-selection-search-input {
    height: 60px !important;
  }
  .ant-select-single.ant-select-show-arrow .ant-select-selection-item,
  .ant-select-single .ant-select-selector .ant-select-selection-item,
  .ant-select-single .ant-select-selector .ant-select-selection-placeholder {
    padding-left: 30px !important;
    line-height: 60px !important;
    font-size: 1.2rem !important;
  }

  .ant-select:focus,
  .ant-select-focused {
    box-shadow: none;
  }

  .ant-select-focused:not(.ant-select-disabled).ant-select:not(.ant-select-customize-input)
    .ant-select-selector {
    box-shadow: none;
    outline: none;
    border-color: #d9d9d9;
  }

  .ant-input-number:focus,
  .ant-input-number-focused {
    box-shadow: none;
    outline: none;
    border: none;
  }

  //Date-picker
  .ant-picker {
    width: 100% !important;
    height: 60px !important;
    padding: 0 30px !important;
    border-radius: 10px !important;
    &:hover,
    &-focused {
      border-color: #d9d9d9 !important;
      box-shadow: none !important;
    }
    &-input {
      padding-left: 15px !important;
      input {
        font-size: 1.2rem !important;
      }
    }
  }
}

.form-search-main {
  //Select
  .ant-select {
    &-arrow {
      top: 41% !important;
    }
  }
  .ant-form-item {
    margin: 0 !important;
  }

  .ant-select-single .ant-select-selector {
    height: 47px !important;
    border-radius: 10px !important;
  }
  .ant-select-single .ant-select-selector .ant-select-selection-search-input {
    height: 47px !important;
  }
  .ant-select-single.ant-select-show-arrow .ant-select-selection-item,
  .ant-select-single .ant-select-selector .ant-select-selection-item,
  .ant-select-single .ant-select-selector .ant-select-selection-placeholder {
    line-height: 47px !important;
    font-size: 0.9rem !important;
  }
}

//Button
.ant-btn {
  padding: 12px;
  border-radius: 8px;
  border: none;
  background-color: var(--color-blue-button);
  color: white;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 1px;
  transition: all 0.15s ease-in-out 0s;
  height: auto;
  min-width: 10rem;
  &:hover,
  &:focus {
    background-color: var(--color-blue-button-hover);
    color: white;
  }
}

//Tooltip
.ant-tooltip {
  max-width: fit-content !important;
}

//Slider
.ant-slider-step {
  background: darkgray !important;
}
.ant-slider-track {
  z-index: 1 !important;
}

//modal-show-image
.modal-show-image {
  border-radius: 18px !important;
  overflow: hidden !important;
  .ant-modal-body {
    background: black !important;
    padding: 0 !important;
    border-radius: 18px !important;
  }
  .ant-modal-close-x {
    font-size: 27px !important;
    color: white !important;
  }
  &-room {
    .ant-modal-close-x {
      color: black !important;
    }
  }
}

//form
.ant-form-item {
  display: block !important;
  &-label {
    text-align: left !important;
  }
}

.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
  height: 40px;
}
.ant-select-single .ant-select-selector .ant-select-selection-item,
.ant-select-single .ant-select-selector .ant-select-selection-placeholder {
  line-height: 40px;
}

.ant-input,
.ant-picker {
  height: 40px !important;
}

//loading
.ant-skeleton-title {
  margin-bottom: 12px !important;
}
.ant-skeleton-paragraph {
  margin-top: 0 !important;
}

.ant-skeleton-reverse {
  .ant-skeleton-content {
    text-align: -webkit-right !important;
  }
}

//rate
.ant-rate {
  display: flex !important;
}

//input
.ant-input {
  height: 56px !important
}
.ant-input:focus, .ant-input-focused {
  box-shadow: none;
}
.ant-input-status-error:not(.ant-input-disabled):not(.ant-input-borderless).ant-input:focus, .ant-input-status-error:not(.ant-input-disabled):not(.ant-input-borderless).ant-input-focused {
  box-shadow: none !important;
  border: 1px solid $color-red-500 !important;
}
.ant-input-status-error:not(.ant-input-disabled):not(.ant-input-borderless).ant-input, .ant-input-status-error:not(.ant-input-disabled):not(.ant-input-borderless).ant-input:hover {
  border: 1px solid $color-red-500 !important;
}

//form-explain
.ant-form-item-explain-error {
  font-weight: 400;
  font-size: 14px;
  color: $color-red-500 !important;
  line-height: 20px;
  margin-bottom: 10px;
}